html {
  background: -webkit-linear-gradient(left, #403b4a, #e7e9bb);
  background: linear-gradient(to right, #403b4a, #e7e9bb);
  font-family: 'Roboto Slab', serif;
  font-size: 2.5vh;
}

#root {
  padding: 0 5%;
  margin: 0 auto;
}

.loadingMassage {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transition: translate(-50%, -50%);
  transition: translate(-50%, -50%);
  font-size: 3vh;
}

.loadingMassageDotFirst,
.loadingMassageDotSecond,
.loadingMassageDotThird {
  -webkit-animation: animation 0.8s linear infinite;
          animation: animation 0.8s linear infinite;
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
  opacity: 0;
  font-size: 4vh;
}

.loadingMassageDotSecond {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.loadingMassageDotThird {
  -webkit-animation-delay: 0.7s;
          animation-delay: 0.7s;
}

@-webkit-keyframes animation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes animation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

