body {
  font-family: 'proxima-nova', sans-serif;
  font-size: 16px;
  a {
    color: inherit;
    text-decoration: none;
  }
}

* {
  outline: 0;
  border: 0;
  margin: 0;
}

.left {
  float: left;
}
.right {
  float: right;
}

$yellow: #ffdd00;

.text-wrapper {
  position: absolute;
  top: 50%;
  width: 100%;
  text-align: center;
  color: white;

  & a {
    text-decoration: underline;
  }
}

.form-wrapper {
  margin: 0 auto;
  margin-top: 120px;
  width: 360px;
  height: auto;
  background-color: black;
  color: white;
  border: 1px solid #161616;
  box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.3);
  position: absolute;
  margin-top: -130px;
  margin-left: -181px;
  top: 50%;
  left: 50%;
}

header {
  height: 46px;
  border-bottom: 1px solid #161616;
  line-height: 46px;
  padding: 0 28px;
  font-size: 0.65em;
  font-weight: 600;
  letter-spacing: 0.3em;
  .logo {
    text-transform: uppercase;
  }
  span {
    color: $yellow;
  }
}

.login-form {
  padding: 18px 28px 0 28px;
  .subtitle {
    font-size: 0.92em;
  }
  input {
    font-size: 1.05em;
    font-weight: 300;
    margin-top: 18px;
    padding: 10px 8px;
    width: 288px;
  }
}

footer {
  padding: 26px 28px 22px 28px;
  font-size: 0.82em;
  .social-login {
    i:first-child {
      margin-left: 4px;
    }
  }
  .form-actions {
    a {
      padding: 4px 8px;
    }
  }
  .register {
    background-color: $yellow;
    color: black;
    border-radius: 2px;
  }
}

body {
  width: 100%;
  height: 100%;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.88);
}

i:hover,
.login:hover {
  color: rgb(0, 0, 0);
}

.register {
  margin-bottom: 5px;
}
